/* harmony default export */ var todolist = ({
  data: function data() {
    return {
      newItem: '',
      items: [{
        label: 'Foo',
        isFinished: true
      }, {
        label: 'Bar',
        isFinished: false
      }],
      newTodoText: '',
      todos: [{
        id: 1,
        title: 'Do the dishes',
        show: false
      }, {
        id: 2,
        title: 'Take out the trash',
        show: false
      }, {
        id: 3,
        title: 'Mow the lawn',
        show: false
      }],
      nextTodoId: 4,
      checkedNames: []
    };
  },

  methods: {
    handleMakeSure: function handleMakeSure(i) {
      this.todos[i].show = !this.todos[i].show;
      // this.todos[i].show = false
      // console.log('i:' + i + event.currentTarget.dataset.a + this.todos[i].title)
    },
    handleWrite: function handleWrite(i) {
      this.todos[i].show = !this.todos[i].show;
      // this.todos[i].show = true
    },

    addNewTodo: function addNewTodo() {
      this.todos.push({
        id: this.nextTodoId++,
        title: this.newTodoText
      });
      this.newTodoText = '';
    },

    addNewItem: function addNewItem() {
      if (!this.newItem.length) {
        alert('不能添加空项目!');
        return;
      }

      this.items.push({
        label: this.newItem,
        isFinished: false
      });

      this.newItem = '';
    },

    deleteOne: function deleteOne(index) {

      var r = confirm("确定删除？");
      if (r === true) {
        this.todos.pop(index, 1);
      } else {}
    },

    deleteOne2: function deleteOne2(index) {

      var r = confirm("确定删除？");
      if (r === true) {
        this.todos.pop(index, 1);
      } else {}
    },

    deleteAll: function deleteAll(index) {

      var r = confirm("确定删除？");
      if (r === true) {
        this.items = [];
      } else {}
    }

  }
});
